/* UPCOMING MOVIES */
.upcoming {
    margin-top: 100px;
    height: auto;
    height: 780px;
}

.upcoming__card--wrapper {
    display: flex;
}

.upcoming__poster--container {
    height: 700px;
    width: 50%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    display: flex;
}

.upcoming__poster--container::after {
    content: "";
    background-color: #111518;
    height: 100%;
    width: 55%;
    background: linear-gradient(
        90deg,
        rgb(17, 21, 24) 0%,
        rgb(17, 21, 24) 30%,
        rgba(17, 21, 24, 0) 100%
    );
}

.upcoming__description {
    width: 50%;
    display: flex;
    flex-direction: column;
}

.upcoming__title {
    font-size: 65px;
    font-weight: bold;
    color: #fff;
    text-align: center;
}

.upcoming__overview {
    display: flex;
    flex-direction: row;
    margin-top: 100px;
}

.overview__title {
    color: #cacaca;
    width: 100px;
    height: 100px;
}

.overview__description {
    color: #cacaca;
    width: 500px;
}